import Vue from "vue";
import Vuex from "vuex";
import router from "@/router";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userInfo: null,
    allUsers:[
      {id:1,name:"hyungjun",email:"khj3265@naver.com",password:"1234"},
      {id:2,name:"hyungjun2",email:"khj32652@naver.com",password:"1234"},
      {id:3,name:"hyungjun3",email:"khj32653@naver.com",password:"1234"}
    ],
    isLogin: false,
    isLoginError: false
  },
  mutations: {
    //로그인 성공시
    loginSuccess(state, payload){
      state.isLogin = true
      state.isLoginError = false
      state.userInfo = payload
    },
    //로그인 실패시
    loginError(state){
      state.isLogin = false
      state.isLoginError = true
    },
    logout(state){
      state.isLogin = false
      state.isLoginError = false
      state.userInfo = null
    }
  },
  actions: {
    //로그인을 시도
    login({ state , commit }, loginObj){
      let selectedUser = null
      console.log(loginObj.email)
      console.log(loginObj.password)
      state.allUsers.forEach(user => {
        if (user.email === loginObj.email) selectedUser = user
      })
      if (selectedUser === null || selectedUser.password !== loginObj.password)
          commit("loginError")
      else {
        commit("loginSuccess", selectedUser)
        router.push({name:"mypage"})
      }
    },
    logout({commit}){
      commit("logout")
      router.push({name:"home"})
    }
  },
  modules: {}
});
